import React, { useState, useEffect, useRef } from "react";
import { Box, Typography, Button, TextField, Grid } from "@mui/material";
import { useNavigate } from "react-router-dom";
import Images from "../../../assets";

const steps = [
  {
    key: "signup",
    title: "Sign Up",
    description:
      "Sign up to take control of your study abroad journey — from discovering universities to getting expert help with applications and visas.",
    buttonText: "Start your journey now",
    leftImage: Images.ReactangleTab,
  },
  {
    key: "profile",
    title: "Create Profile",
    description:
      "Create your profile to get personalized university matches, track your applications, and receive expert support tailored to your goals.",
    buttonText: "Create Now",
    leftImage: Images.CreateTab
  },
  {
    key: "recommend",
    title: "Generate Recommendations",
    description:
      "Share your academic background, extracurriculars, and preferences to get personalized recommendations.",
    buttonText: "Generate Recommendation",
    leftImage: Images.GenerateTab
  },
];

const SignUpSection = () => {
  const navigate = useNavigate();
  const [phoneNumber, setPhoneNumber] = useState("");
  const [currentStep, setCurrentStep] = useState(0);
  const [scrollY, setScrollY] = useState(0);
  const [isHovered, setIsHovered] = useState(false);
  const autoPlayRef = useRef<NodeJS.Timeout | null>(null);
  
  const { title, description, buttonText, leftImage } = steps[currentStep];

  // Auto-play functionality
  useEffect(() => {
    const startAutoPlay = () => {
      if (!isHovered) {
        autoPlayRef.current = setInterval(() => {
          setCurrentStep((prevStep) => {
            const nextStep = (prevStep + 1) % steps.length;
            return nextStep;
          });
        }, 1000); // Change every 1 second
      }
    };

    const stopAutoPlay = () => {
      if (autoPlayRef.current) {
        clearInterval(autoPlayRef.current);
        autoPlayRef.current = null;
      }
    };

    if (isHovered) {
      stopAutoPlay();
    } else {
      startAutoPlay();
    }

    return () => {
      stopAutoPlay();
    };
  }, [isHovered]);

  // Handle scroll events
  useEffect(() => {
    const handleScroll = () => {
      const scrollPosition = window.scrollY;
      setScrollY(scrollPosition);
      
      // Calculate which step to show based on scroll position
      const sectionHeight = window.innerHeight;
      const stepThreshold = sectionHeight * 0.5; // Change step when scrolled 50% of viewport height
      
      const newStep = Math.min(
        Math.floor(scrollPosition / stepThreshold),
        steps.length - 1
      );
      
      setCurrentStep(prevStep => {
        if (newStep !== prevStep) {
          return newStep;
        }
        return prevStep;
      });
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []); // Remove currentStep dependency to prevent infinite loop

  const nextStep = () => {
    if (currentStep < steps.length - 1) {
      setCurrentStep(currentStep + 1);
    } else {
      // If it's the last step, navigate to startpage
      navigate("/startpage");
    }
  };

  const handleFormSubmit = () => {
    navigate("/startpage");
  };

  const handleMouseEnter = () => {
    setIsHovered(true);
  };

  const handleMouseLeave = () => {
    setIsHovered(false);
  };

  return (
    <Box
      sx={{
        width: "100%",
        height: { xs: "auto", md: "100vh" }, // Full viewport height for scroll-based changes
        position: "relative",
        background: "white",
        overflow: "hidden",
      }}
      onMouseEnter={handleMouseEnter}
      onMouseLeave={handleMouseLeave}
    >
      {/* Background */}
      <Box
        sx={{
          width: "100%",
          height: { xs: "0%", md: "90%" },
          position: "absolute",
          background: "#F5EEE4",
        }}
      />

      {/* Orange Shape Background with Signup_bg image */}
      <Box
        sx={{
          width: { xs: "140%", md: 872 },
          height: { xs: 280, md: 627 },
          left: { xs: "-20%", md: 900, lg: 1000 },
          top: { xs: 0, md: 84 },
          position: "absolute",
          background: `url(${Images.SignUpBg})`,
          backgroundSize: "cover",
          backgroundPosition: "center",
          backgroundRepeat: "no-repeat",
          // borderRadius: "60% 40% 30% 70% / 60% 30% 70% 40%",
        }}
      />

      {/* Content Container */}
      <Box
        sx={{
          left: { xs: 16, md: 900, lg: 1100 },
          top: { xs: 56, md: 238.5 },
          position: { xs: "absolute", md: "absolute" },
          flexDirection: "column",
          justifyContent: "flex-start",
          alignItems: "flex-start",
          gap: { xs: 2, md: 6 },
          display: "inline-flex",
          width: { xs: "calc(100% - 32px)", md: "auto" },
          zIndex: 2,
        }}
      >
        {/* Text Content */}
        <Box
          sx={{
            width: { xs: "100%", md: 579 },
            flexDirection: "column",
            justifyContent: "flex-start",
            alignItems: "flex-start",
            gap: { xs: 1, md: 1 },
            display: "flex",
          }}
        >
          <Typography
            sx={{
              color: "white",
              fontSize: { xs: 28, sm: 32, md: 44 },
              fontFamily: "Neutrif Studio",
              fontWeight: "700",
              wordWrap: "break-word",
            }}
          >
            {title}
          </Typography>
          <Typography
            sx={{
              alignSelf: "stretch",
              color: "rgba(255, 255, 255, 0.90)",
              fontSize: { xs: 14, sm: 16, md: 18 },
              fontFamily: "Neutrif Studio",
              fontWeight: "400",
              lineHeight: { xs: "24px", sm: "28px", md: "45.44px" },
              wordWrap: "break-word",
            }}
          >
            {description}
          </Typography>
        </Box>

        {/* Button */}
        <Button
          onClick={nextStep}
          sx={{
            paddingLeft: { xs: 2.25, md: 3.375 },
            paddingRight: { xs: 2.25, md: 3.375 },
            paddingTop: { xs: 1.5, md: 2.5 },
            paddingBottom: { xs: 1.5, md: 2.5 },
            background: "white",
            borderRadius: 4.25,
            outline: "2px solid #F5EEE4",
            justifyContent: "center",
            alignItems: "center",
            gap: 1.25,
            display: "inline-flex",
            textTransform: "none",
            "&:hover": {
              background: "#F8F9FA",
            },
          }}
        >
          <Typography
            sx={{
              textAlign: "center",
              justifyContent: "flex-end",
              display: "flex",
              flexDirection: "column",
              color: "#495052",
              fontSize: { xs: 14, sm: 16, md: 22 },
              fontFamily: "Neutrif Studio",
              fontWeight: "700",
              wordWrap: "break-word",
            }}
          >
            {buttonText}
          </Typography>
        </Button>
      </Box>

      {/* Dynamic Left Image */}
      <Box
        component="img"
        sx={{
          width: { xs: "85%", md: 654.32 },
          height: { xs: "auto", md: 483 },
          left: { xs: "50%", md: 80 },
          transform: { xs: "translateX(-50%)", md: "none" },
          top: { xs: 300, md: 143 },
          position: "absolute",
          transition: "opacity 0.3s ease-in-out",
          zIndex: 1,
        }}
        src={leftImage}
        alt="Dynamic Content"
      />

      {/* Scroll Indicator (optional) */}
      {/* <Box
        sx={{
          position: "fixed",
          bottom: 20,
          right: 20,
          background: "rgba(0,0,0,0.7)",
          color: "white",
          padding: 1,
          borderRadius: 1,
          fontSize: "0.75rem",
          zIndex: 1000,
        }}
      >
        Step {currentStep + 1} of {steps.length} | Auto: {!isHovered ? "ON" : "PAUSED"} | Scroll Y: {Math.round(scrollY)}
      </Box> */}
    </Box>
  );
};

export default SignUpSection; 