import React, { useState } from "react";
import { Box, Typography, Button, Grid, TextField, Autocomplete, Select, MenuItem, FormControl, Chip } from "@mui/material";
import SearchIcon from '@mui/icons-material/Search';
import { useNavigate } from "react-router-dom";
import Images from "src/assets";
import { countryList } from "src/assets/menu";
import { getExploreUrl } from "src/assets/library";

// Popular Courses data (from CourseSelection.tsx)
const popularCourses = [
  { name: "Business Administration", value: "Business Administration" },
  { name: "Computer Sciences", value: "Computer Sciences" },
  { name: "Artificial Intelligence", value: "Artificial Intelligence" },
  { name: "Data Science & Big Data", value: "Data Science & Big Data" },
  { name: "Cyber Security", value: "Cyber Security" },
  { name: "Mechanical Engineering", value: "Mechanical Engineering" },
  { name: "Electrical Engineering", value: "Electrical Engineering" },
  { name: "Biomedical Science", value: "Biomedical Science" },
  { name: "Software Engineering", value: "Software Engineering" },
  { name: "International Business", value: "International Business" },
  { name: "Medicine", value: "Medicine" },
  { name: "Finance", value: "Finance" },
  { name: "Marketing", value: "Marketing" },
  { name: "Psychology", value: "Psychology" },
  { name: "Law", value: "Law" },
  { name: "Economics", value: "Economics" }
];

const ProgramSearchSection = () => {
  const navigate = useNavigate();

  // Form state
  const [formData, setFormData] = useState({
    degreeType: '',
    countries: [] as string[],
    subjects: [] as string[]
  });

  // Degree type options
  const degreeTypeOptions = [
    { label: "Bachelors", value: "Bachelors" },
    { label: "Masters", value: "Masters" }
  ];

  // Handle form field changes
  const handleChange = (field: string, value: any) => {
    setFormData(prev => ({
      ...prev,
      [field]: value
    }));
  };

  // Handle form submission
  const handleFindProgram = () => {
    console.log('Form Data:', formData);
    
    // Create filters array based on selected form data
    const filters: any[] = [];
    
    // Add degree type to localStorage (for other parts of the app)
    if (formData.degreeType) {
      const currentProfile = localStorage.getItem("user-profile");
      const currentProfileObj = currentProfile ? JSON.parse(currentProfile) : {};
      localStorage.setItem("user-profile", JSON.stringify({
        ...currentProfileObj,
        preference: {
          ...currentProfileObj.preference,
          degree: formData.degreeType
        }
      }));
    }
    
    // Add country filter if countries are selected
    if (formData.countries.length > 0) {
      const countryFilter = {
        type: "country",
        data: formData.countries.map(country => ({
          name: country,
          value: country
        }))
      };
      filters.push(countryFilter);
    }
    
    // Add subject filter if subjects are selected
    if (formData.subjects.length > 0) {
      const subjectFilter = {
        type: "subDiscipline",
        data: formData.subjects.map(subject => ({
          name: subject,
          value: subject
        }))
      };
      filters.push(subjectFilter);
    }
    
    // Navigate to explore page using the existing getExploreUrl function
    navigate(getExploreUrl("filterData", filters));
  };

  return (
    <Box
      sx={{
        background: "white",
        py: { xs: 6, lg: 10 },
        position: "relative",
        overflow: "visible"
      }}
    >
      {/* Illustration positioned absolutely */}
      <Box
        sx={{
          position: "absolute",
          left: { xs: "-30px", sm: "-50px", md: "-80px", lg: "-50px" },
          top: "10%",
          transform: "translateY(-50%)",
          zIndex: 10,
          display: { xs: "none", sm: "block" }
        }}
      >
        <img 
          src={Images.FindIllu} 
          alt="Find Perfect Program Illustration"
          style={{ 
            width: "280px",
            height: "220px",
            objectFit: "contain"
          }}
        />
      </Box>

      <Box
        sx={{
          // maxWidth: "1440px",
          width: "100%",
          // mx: "auto",
          // px: { xs: 2, sm: 3, md: 4, lg: 6 },
          position: "relative",
          zIndex: 2
        }}
      >
        <Box sx={{
          background:"#EFF4F7",
          height: { xs: "120px", sm: "140px", md: "150px" },
          display: "flex",
          alignItems: "center",
          justifyContent: "center"
        }}>
        <Typography
          variant="h2"
          sx={{
            textAlign: "center",
            fontSize: { xs: "1.5rem", sm: "2rem", md: "2.5rem", lg: "3rem" },
            fontWeight: 500,
            color: "#495052",
            px: { xs: 2, sm: 0 }
          }}
        >
          Find the perfect program
        </Typography>
        </Box>
        {/* Main Heading */}
       

        {/* Search Section */}
        <Box
          sx={{
            background: "#70A6B2",
            // borderRadius: "20px",
            p: { xs: 2, sm: 3, md: 4 },
            // mt: { xs: 2, sm: 3, md: 4 }
          }}
        >
          <Grid container spacing={{ xs: 2, sm: 3 }} alignItems="end">
            <Grid item xs={12} sm={6} md={3}>
              <Typography
                variant="body2"
                sx={{
                  color: "#FFFF",
                  mb: 1,
                  fontWeight: 500,
                  fontSize:"20px"
                }}
              >
                Degree type
              </Typography>
              <FormControl fullWidth>
                <Select
                  value={formData.degreeType}
                  onChange={(e) => handleChange('degreeType', e.target.value)}
                  displayEmpty
                  sx={{
                    background: "white",
                    borderRadius: "12px",
                    "& fieldset": {
                      borderColor: "transparent",
                    },
                    fontSize: "14px"
                  }}
                >
                  <MenuItem value="" disabled>
                    <span style={{ color: '#999' }}>Select a degree type</span>
                  </MenuItem>
                  {degreeTypeOptions.map((option) => (
                    <MenuItem key={option.value} value={option.value}>
                      {option.label}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
            <Grid item xs={12} sm={6} md={3}>
              <Typography
                variant="body2"
                sx={{
                  color: "#FFF",
                  mb: 1,
                  fontWeight: 500,
                  fontSize:"20px"
                }}
              >
                Location
              </Typography>
              <Autocomplete
                multiple
                options={countryList}
                value={formData.countries}
                onChange={(event, value) => handleChange("countries", value)}
                renderInput={(params) => (
                  <TextField
                    {...params}
                    placeholder="Search countries"
                    sx={{
                      "& .MuiOutlinedInput-root": {
                        background: "white",
                        borderRadius: "12px",
                        padding: '4px 8px',
                        "& fieldset": {
                          borderColor: "transparent",
                        }
                      }
                    }}
                  />
                )}
                renderTags={(value, getTagProps) =>
                  value.map((option, index) => (
                    <Chip
                      {...getTagProps({ index })}
                      label={option}
                      size="small"
                      sx={{
                        bgcolor: '#70A6B2',
                        color: 'white',
                        '& .MuiChip-deleteIcon': {
                          color: 'white'
                        }
                      }}
                    />
                  ))
                }
                ListboxProps={{
                  style: { maxHeight: 200 }
                }}
              />
            </Grid>
            <Grid item xs={12} sm={6} md={3}>
              <Typography
                variant="body2"
                sx={{
                  color: "#FFFF",
                  mb: 1,
                  fontWeight: 500,
                  fontSize:"20px"
                }}
              >
                Subject
              </Typography>
              <Autocomplete
                multiple
                options={popularCourses}
                getOptionLabel={(option) => option.name}
                value={formData.subjects.map(subject => ({ name: subject, value: subject }))}
                onChange={(event, value) => handleChange("subjects", value.map(v => v.name))}
                renderInput={(params) => (
                  <TextField
                    {...params}
                    placeholder="Search programs"
                    sx={{
                      "& .MuiOutlinedInput-root": {
                        background: "white",
                        borderRadius: "12px",
                        padding: '4px 8px',
                        "& fieldset": {
                          borderColor: "transparent",
                        }
                      }
                    }}
                  />
                )}
                renderTags={(value, getTagProps) =>
                  value.map((option, index) => (
                    <Chip
                      {...getTagProps({ index })}
                      label={option.name}
                      size="small"
                      sx={{
                        bgcolor: '#70A6B2',
                        color: 'white',
                        '& .MuiChip-deleteIcon': {
                          color: 'white'
                        }
                      }}
                    />
                  ))
                }
                ListboxProps={{
                  style: { maxHeight: 200 }
                }}
              />
            </Grid>
            <Grid item xs={12} sm={6} md={3}>
              <Button
                variant="contained"
                fullWidth
                onClick={handleFindProgram}
                sx={{
                  background: "#F48438",
                  color: "white",
                  borderRadius: "12px",
                  py: { xs: 1.2, sm: 1.5 },
                  fontSize: { xs: "0.9rem", sm: "1rem" },
                  fontWeight: 600,
                  textTransform: "none",
                  "&:hover": {
                    background: "#F48438"
                  }
                }}
              >
                Find Program
              </Button>
            </Grid>
          </Grid>
        </Box>

        {/* Recommendation CTA */}
        <Box sx={{background:"#EFF4F7",height:"150px",
        display:"flex",justifyContent:"center",alignItems:"center"
        }}>
          <Box
          sx={{
            display: "flex",
            flexDirection: { xs: "column", sm: "row" },
            justifyContent: "center",
            alignItems: { xs: "flex-start", sm: "center",lg:"center" },
            gap: 2,
            borderRadius:"30px",
            background:"#FFFF",
            px:3,
            py:2
          }}
        >
          <Typography
            variant="h6"
            sx={{
              color: "#495052",
              fontSize: { xs: "1.125rem", md: "1.25rem" },
              fontWeight: 500
            }}
          >
            Can't decide? Let AI find your best-fit
          </Typography>
          <Button
            variant="outlined"
            onClick={() => navigate("/startpage")}
            sx={{
              border: "2px solid #F48438",
              color: "#495052",
              borderRadius: "25px",
              px: 3,
              py: 1,
              fontSize: "1rem",
              fontWeight: 600,
              textTransform: "none",
              "&:hover": {
                border: "2px solid #F48438",
                background: "rgba(255, 107, 53, 0.05)"
              }
            }}
          >
            Generate Recommendation
          </Button>
        </Box>
        </Box>
      
      </Box>
    </Box>
  );
};

export default ProgramSearchSection; 